/* Extras
========================================================================== */
.clear:after {
  content: "";
  clear: both;
  display: block;
  visibility: hidden;
  overflow: hidden;
}

.tb {
  display: none;
}

.tbpc {
  display: none;
}

.pc {
  display: none;
}

.mimg img {
  width: 100%;
  height: auto;
}

.screen {
  display: none;
  opacity: 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 130%;
  background-color: #000;
  z-index: 80;
}

.screen_active .screen {
  animation-delay: 0.2s;
  -webkit-animation-delay: 0.2s;
  animation-name: screen_active;
  -webkit-animation-name: screen_active;
  animation-duration: 0.3s;
  -webkit-animation-duration: 0.3s;
  animation-iteration-count: 1;
  -webkit-animation-iteration-count: 1;
  animation-fill-mode: both;
  -webkit-animation-fill-mode: both;
}

@keyframes screen_active {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 0.8;
  }
}

@-webkit-keyframes screen_active {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 0.8;
  }
}

